section {
	display: flex;
	flex-direction: column;
	position: relative;
	color: white;
	@include tablet {
		// padding: 0 100px;
	}
	h1 {
		font-size: 5rem;
		margin-bottom: 1rem;
	}
	p,
	ul {
		font-size: 2rem;
		line-height: 1.5;
		margin-bottom: 2rem;
		// text-align: justify;
		// text-justify: inter-word;
	}
	ul {
		// list-style: inside;
		z-index: 1;
		// text-align: right;
		margin-right: 2rem;
		@include tablet {
			width: calc(40%);
			// max-width: 500px;
		}
	}
}
.desktop {
	display: none !important;
}
@include tablet {
	.mobile {
		display: none !important;
	}
	.desktop {
		display: block !important;
	}
}

section#about,
section#meshutaf,
section#social-justice,
section#kibbutzi,
section#nachshonim,
section#hero,
section#contact {
	scroll-margin-top: 50px;
	> div:first-of-type {
		padding: 2rem 2rem 0 2rem;
		width: 100%;
		display: flex;
		flex-direction: column;
		@include tablet {
			display: flex;
			justify-content: center;
			padding: 2rem 10vw;
			min-height: 30vh;
		}
		> img {
			width: 120%;
			align-self: center;
			// transform: translateY(10%);
		}
		h1,
		p {
			@include tablet {
				width: calc(40% + 2rem);
				// max-width: 500px;
			}
		}
	}
	> div:last-of-type {
		img {
			position: relative;
			display: block;
		}
	}
}
