nav {
	position: fixed;
	z-index: 5;
	width: 100%;
	height: 50px;
	top: 0;
	right: 0;
	background: $blue;
	// border-bottom: 3px solid white;

	@include tablet {
		border-bottom: none;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		height: 7vh;
	}
	> div:first-child {
		border-bottom: 3px solid white;
		@include tablet {
			border-bottom: 3px solid white;
			width: 10%;
			height: 100%;
			margin: 0;
			padding-right: 2rem;
			min-width: 150px;
		}
		display: flex;
		align-items: center;
		justify-content: space-between;
		// margin: 0 5vw 0 5vw;
		padding: 0.5rem 5vw 0 5vw;
		// position: relative;
		background: $blue;
		button.menu {
			background: none;
			border: none;
			@include tablet {
				display: none;
			}
			span {
				display: block;
				height: 3px;
				width: 30px;
				background: white;
				margin: 5px 0;
				border-radius: 1px;
			}
		}
		> a {
			// position: relative;
			@include tablet {
				height: 100%;
			}

			img {
				width: 50%;
				margin-top: 4%;
				// position: absolute;
				// bottom: 0;
				display: block;
				@include tablet {
					margin-top: 0%;
					height: 100%;
					width: auto;
				}
			}
		}
	}
}
nav div + div {
	// transform:scale(0);
	font-size: 2rem;
	height: 0px;
	// opacity: 0;
	display: flex;
	flex-direction: column;
	position: fixed;
	background: $blue;
	z-index: 2;
	width: 100%;
	text-align: center;
	@include tablet {
		flex-direction: row;
		height: 100%;
		position: relative;
		justify-content: space-between;
		// padding: 0 3vw;
	}
	> a {
		opacity: 0;
		pointer-events: none;
		padding: 1rem 0;
		transition: opacity 100ms;
		@include tablet {
			position: relative;
			border-bottom: 3px solid white;
			width: 100%;
			transform: scale(1);
			opacity: 1;
			pointer-events: all;
			height: 100%;
			padding: 0 3rem;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		button {
			font-size: 2rem;
			background: white;
			border: 2px solid white;
			padding: 0.5rem 2rem;
			border-radius: 3px;
			color: $blue;
			font-weight: bold;
			cursor: pointer;
			transition: box-shadow 100ms, transform 100ms;
			@include tablet {
				background: white;
				border: white;
				color: $blue;
				// position: absolute;
				// bottom: 0;
				// border-radius: 3px 3px 0 0;
				// padding: 1rem 2rem 2rem 2rem;
			}
		}
	}
	> a:hover {
		background: white;
		color: $blue;
		@include tablet {
			border-bottom: 3px solid $blue;
		}
		transition: background 200ms, border 200ms;
	}
	// > a:last-child{
	// 	border: 3px white solid;
	// 	// box-shadow: 1px 1
	// }
	> a:last-child:hover {
		color: $blue;
		background: $blue;
		@include tablet {
			color: $blue;
			background: initial;
			border-bottom: 3px solid white;
		}
		// border: 1px solid white;
	}
	button:hover,
	button:focus {
		box-shadow: 0px 4px 2px 1px rgba(0, 0, 0, 0.3);
		transform: translateY(-3px);
	}
	transition: transform 300ms, height 300ms;
	transform-origin: top;
}

div + div.show {
	transform: scale(1);
	// position: relative;
	// opacity: 1;
	height: 30rem;
	// display: flex;
	justify-content: space-between;
	padding: 1rem 0;
	a {
		transition: opacity 300ms 200ms;
		// height: 100%;
		transform: scale(1);
		opacity: 1;
		pointer-events: all;
		// display: block;
	}
}

